#root {
  margin: 0;
}
body {
  background-color: rgb(49, 49, 146);
}

.base {
  width: 80%;
  background-color: white;
  min-height: 700px;
  margin: auto;
  margin-top: 50px;
  border-radius: 20px;
  padding-top: 20px;
  margin-bottom: 50px;
}

.title {
  text-align: center;
  color: white;
  background-color: rgb(99, 0, 96);
}

.btn {
  background-color: rgb(99, 0, 96);
  color: aliceblue;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  border: 0;
  font-size: medium;
  margin-left: 10px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.btn:hover {
  background-color: green;
}

.inputf {
  width: 80%;
  border: 0;
  border-bottom: 2px solid pink;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.input {
  margin: auto;
  width: 80%;
  text-align: center;
}

.task {
  display: flex;
  margin-top: 50px;
  flex-direction: row;
  text-align: left;
}
.task .btn {
  width: 25px;
  height: 25px;
  text-align: center;
  margin: 0;
  padding: 0;
}
.task .btn:hover {
  background-color: red;
}
ul {
  margin: 0;
  text-align: left;
  list-style-type: none;
  padding: 0%;
}
li {
  margin-left: 20px;
}

.x {
  display: flex;
}
